const Pagination = ({ postsPerPage, totalPosts, paginate, currentPage }) => {
  const pageNumbers = []

  const totalPages = Math.ceil(totalPosts / postsPerPage)

  const currentPageStyle = (number) => {
    if (number === currentPage){
      return { backgroundColor: '#DDD' }
    }
  }

  for(let i = 1; i <= totalPages; ++i) {
    pageNumbers.push(i)
  }

  /* eslint-disable */
  return (
    <div className="pagination-nav">
      <a onClick={currentPage === 1 ? null : () => paginate(currentPage-1)} href="#!" className="arrow"><img src={require('../img/left-arrow.png')}/></a>
      <ul className="pagination-numbers">
        {pageNumbers.map(number => (
          <li key={number} className="page-item">
            <a onClick={() => paginate(number)} style={currentPageStyle(number)} href="#!" className="page-link">
              {number}
            </a>
          </li>
        ))}
      </ul>
      <a onClick={currentPage === totalPages ? null :() => paginate(currentPage+1)} href="#!" className="arrow"><img src={require('../img/right-arrow.png')}/></a>
    </div>
  )
  /* eslint-enable */
}

export default Pagination