import { Tooltip, IconButton } from '@mui/material'
import HelpOutlineIcon from '@mui/icons-material/HelpOutline'
import LogoutIcon from '@mui/icons-material/Logout'

const tooltipText = `
'Blog List App is a full stack app built using the MERN stack.
It integrates a REST API with MongoDB and a React front-end.
It is used to share links to your favourite blogs with other users.
Users can create and remove links, as well as vote for their favourite links.
The source code for this project is linked in the footer.
`

const Header = ({ handleLogout, user }) => {
  const logoutButtonStyle = { display: user ? '' : 'none' }

  return (
    <div className="header flex-space-between">
      <div className="thirty-width">
        <h2>Blog List App</h2>
      </div>
      <Tooltip title={tooltipText} placement="bottom">
        <IconButton>
          <HelpOutlineIcon />
        </IconButton>
      </Tooltip>
      <div  style={logoutButtonStyle} className='thirty-width align-right'>
        <button className="logout-btn" onClick={handleLogout}>
          Logout <LogoutIcon />
        </button>
      </div>
    </div>
  )
}

export default Header