import { useEffect, useRef, useState } from 'react'
import Bloglist from './Bloglist'
import BlogForm from './BlogForm'
import Togglable from './Togglable'
import blogService from '../services/blogs'
import Pagination from './Pagination'


const Blogs = ({ setNotificationMessage, setErrorMessage, user, blogs, setBlogs }) => {
  const [sortBy, setSortBy] = useState('popular')
  const [currentPage, setCurrentPage] = useState(1)
  const [postsPerPage] = useState(8)

  // Sends POST request to API containing blog post object
  const createBlog = (blogObject, title, author) => {
    blogFormRef.current.toggleVisibility()
    blogService
      .create(blogObject)
      .then(response => {
        setBlogs(blogs.concat(response.data))
        console.log(response)
        setNotificationMessage(`Created new blog ${title} by ${author}`)
        setTimeout(() => {
          setNotificationMessage(null)
        }, 5000)
      })
  }
  // Sends PUT request to API containing updated blog post object
  const updateBlog = (blogId, changedBlog) => {
    blogService
      .update(blogId, changedBlog)
      .then(response => {
        setBlogs(blogs.filter(n => n.id !== blogId).concat(response))
      })
      .catch(error => { // eslint-disable-line
        setErrorMessage(
          'Blog was already removed from server'
        )
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
        setBlogs(blogs.filter(n => n.id !== blogId))
      })
  }
  // Sends DELETE request to API with blog post ID
  const removeBlog = (blogId, title, author) => {
    blogService
      .remove(blogId)
      .then(response => { // eslint-disable-line
        setBlogs(blogs.filter(blog => blog.id !== blogId))
        setNotificationMessage(`Deleted blog ${title} by ${author}`)
        setTimeout(() => {
          setNotificationMessage(null)
        }, 5000)
      })
      .catch(error => { // eslint-disable-line
        setErrorMessage(
          'Blog was already removed from server'
        )
        setNotificationMessage(`Deleted blog ${title} by ${author}`)
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
        setBlogs(blogs.filter(n => n.id !== blogId))
      })
  }

  const blogFormRef = useRef()

  let sortedBlogsList = blogs.filter(blog => blog.hasOwnProperty('user')) // eslint-disable-line

  // Filters blog posts to only include those posted by a User
  if (sortBy === 'popular') {
    sortedBlogsList = blogs
      .filter(blog => blog.hasOwnProperty('user')) // eslint-disable-line
      .sort((a, b) => b.likes - a.likes)
  } else if (sortBy === 'new') {
    sortedBlogsList = blogs
      .filter(blog => blog.hasOwnProperty('user')) // eslint-disable-line
      .sort((a, b) => new Date(b.date) - new Date(a.date))
  }

  useEffect(() => {
    console.log('Working!')
  }, [sortBy])

  const boldWhenChosen = (sortType) => {
    return { fontWeight: sortBy === sortType ? 'bold' : 'normal' }
  }

  // Get current posts for pagination
  const indexOfLastPost = currentPage * postsPerPage
  const indexOfFirstPost = indexOfLastPost - postsPerPage
  const currentPosts = sortedBlogsList.slice(indexOfFirstPost, indexOfLastPost)

  // Change page
  const paginate = (pageNumber) => setCurrentPage(pageNumber)
  return(
    <div className='full-width'>
      <h3>Logged in as <b>{user.name}</b></h3>
      <Togglable
        buttonLabel="add new blog"
        containerClassName="new-blog"
        buttonClassName='new-blog-button'
        cancelButtonClass='new-blog-cancel'
        ref={blogFormRef}>
        <BlogForm
          createBlog={createBlog}
        />
      </Togglable>
      <hr className='main-divider'/>

      Sort by:
      <a style={boldWhenChosen('new')} onClick={() => setSortBy('new')} href="#!"> New </a>
      /
      <a style={boldWhenChosen('popular')} onClick={() => setSortBy('popular')} href="#!"> Popular</a>

      <div className="blog-list">
        {currentPosts.map(blog =>
          <Bloglist
            key={blog.id}
            blog={blog}
            updateBlog={updateBlog}
            user={user}
            blogUser={blog.user}
            removeBlog={removeBlog}
          />
        )}
      </div>
      <div className="pagination-container">
        <Pagination
          postsPerPage={postsPerPage}
          totalPosts={sortedBlogsList.length}
          paginate={paginate}
          currentPage={currentPage}
        />
      </div>
    </div>
  )
}

export default Blogs