import { useState, forwardRef, useImperativeHandle } from 'react'
import PropTypes from 'prop-types'

const Togglable = forwardRef((props, refs) => {
  const [visible, setVisible] = useState(false)
  // const [divClass, setdivClass] = useState('begin')

  const hideWhenVisible = { display: visible ? 'none' : '' }
  const showWhenVisible = { display: visible ? '' : 'none' }

  const toggleVisibility = () => {
    setVisible(!visible)
  }

  useImperativeHandle(refs, () => {
    return {
      toggleVisibility
    }
  })

  return (
    <div className={props.containerClassName}>
      <div className='full-width flex-column-center' style={hideWhenVisible}>
        <button className={'lrg-btn ' + props.buttonClassName} id="button-shadow" onClick={toggleVisibility}>{props.buttonLabel}</button>
      </div>
      <div className="flex-column-center" style={showWhenVisible}>
        {props.children}
        <button className={'lrg-btn white-bg ' + props.cancelButtonClass} id="button-shadow" onClick={toggleVisibility}>Cancel</button>
      </div>
    </div>
  )
})

Togglable.propTypes = {
  buttonLabel: PropTypes.string.isRequired
}

Togglable.displayName = 'Togglable'

export default Togglable