import { useState } from 'react'

const Blog = ({ blog, updateBlog, user, blogUser, removeBlog }) => {
  const [blogLikes, setBlogLikes] = useState(blog.likes)
  const [detailsVisible, setDetailsVisible] = useState(false)

  const hideWhenVisible = { display: detailsVisible ? 'none' : '' }
  const showWhenVisible = { display: detailsVisible ? '' : 'none' }

  const sameUser = { display: blog.user.id === user.userId ? '' : 'none' }

  const addLikes = (event) => {
    event.preventDefault()
    setBlogLikes(blogLikes + 1)
    const changedBlog = {
      user: blogUser,
      likes: blogLikes + 1,
      author: blog.author,
      title: blog.title,
      url: blog.url
    }
    updateBlog(blog.id, changedBlog)
  }

  const deleteBlog = (event) => {
    event.preventDefault()
    if (window.confirm(`Remove blog ${blog.title} by ${blog.author}?`)) {
      removeBlog(blog.id, blog.title, blog.author)
    }
  }

  const dateFormat = (date) => {
    return(
      `${new Date(date).toDateString().split(' ').slice(0,3).join(' ')}
      ${new Date(date).toTimeString().split(':').slice(0,2).join(':')}`
    )
  }

  return (
    <div>
      <div id="button-shadow" className="closed-card">
        <a href={blog.url} target="_blank" rel="noopener noreferrer"><h5>{blog.title}</h5></a>
        <hr/>
        <div className='card-two-column subtitle flex-space-between'>
          <div className="seventy-width">By {blog.author}</div>
          <div>{blogLikes} likes</div>
        </div>
        <div className="card-user-info" style={showWhenVisible}>
          <hr/>
          <div className='flex-space-between'>
            <div>Posted by <b>{blog.user.name === user.username ? 'you' : blog.user.name}</b></div>
            <div>On {dateFormat(blog.date)}</div>
          </div>
          <button className="sml-btn remove-btn" style={sameUser} onClick={deleteBlog}>remove</button>
        </div>
        <div className='card-two-column flex-space-between'>
          <button className="sml-btn" onClick={addLikes} id="button-shadow">like</button>
          <button className="sml-btn white-bg" id="button-shadow" style={hideWhenVisible} onClick={() => setDetailsVisible(true)}>See Details</button>
          <button className="sml-btn white-bg" id="button-shadow" style={showWhenVisible} onClick={() => setDetailsVisible(false)}>Hide Details</button>
        </div>
      </div>
    </div>
  )
}

export default Blog